@import url(https://fonts.googleapis.com/css?family=Saira+Extra+Condensed:500,700);
@import url(https://fonts.googleapis.com/css?family=Muli:400,400i,800,800i);
@media (min-width: 992px) {
  #sideNav {
    text-align: center;
    position: fixed;
    display: flex;
    flex-direction: column;
    width: 17rem;
    height: 100vh;
    top: 0;
    left: 0;
  }

  #sideNav .navbar-brand {
    display: flex;
    margin: auto auto 0;
    padding: 0.5rem;
  }

  #sideNav .img-profile {
    max-width: 10rem;
    max-height: 10rem;
    border: 0.5rem solid rgba(255, 255, 255, 0.2);
  }

  #sideNav .navbar-collapse {
    display: flex;
    align-items: flex-start;
    flex-grow: 0;
    width: 100%;
    margin-bottom: auto;
  }

  #sideNav .navbar-nav {
    flex-direction: column;
    width: 100%;
  }
}

#sideNav .nav-link {
  font-weight: 800;
  letter-spacing: 0.05rem;
  text-transform: uppercase;
}

#sideNav .navbar-toggler:focus {
  outline-color: #6A9FC8;
}

.icon-with-link {
  display: inline-block;
  height: 3.5rem;
  width: 3.5rem;
  background-color: #495057;
  color: #fff !important;
  border-radius: 100%;
  text-align: center;
  font-size: 1.5rem;
  line-height: 3.5rem;
  margin-right: 1rem;
}

.icon-with-link:last-child {
  margin-right: 0;
}

.icon-with-link:hover {
  background-color: #4479a2;
}

.list-dot {
  list-style-type: disc;
}

.list-none {
  list-style-type: none;
}

.fa-ul > li {
  margin-bottom: 0.5em;
}

/* hack for aligning fontawesome icons in li elements vertically */
.fa-li {
  top: 0.22em;
}

.resume-extra {
  line-height: 2rem;
}

section.resume-section {
  padding-top: 5rem !important;
  padding-bottom: 5rem !important;
  max-width: 75rem;
}

@media (min-width: 768px) {
  section.resume-section {
    min-height: 100vh;
  }

  section.resume-section .resume-extra {
    min-width: 18rem;
  }
}

@media (min-width: 992px) {
  section.resume-section {
    padding-top: 3rem !important;
    padding-bottom: 3rem !important;
  }
}

.btn-concerts.btn-primary:hover,
.btn-concerts.btn-primary:active,
.btn-concerts.btn-primary:visited,
.btn-concerts.btn-primary:focus {
  color: rgba(255, 255, 255, 0.85);
  background-color: #4479a2;
  border-color: #4479a2;
}


.btn-concerts.btn-primary {
  color: rgba(255, 255, 255, 0.65);
  background-color: #4479a2;
  border-color: #4479a2;
}


.vertical-timeline-element-content {
  border-top: 3px solid #4479a2;
}

@media only screen and (max-width: 1169px) {
  /* .vertical-timeline--animate */
  .vertical-timeline-element-content.bounce-in {
    visibility: visible;
    -webkit-animation: cd-bounce-2-inverse 0.6s;
    animation: cd-bounce-2-inverse 0.6s;
  }
}

.silver {
  color: #C0C0C0;
}

.fa-graduation-cap {
  color: #000;
}

.fa-award {
  color: #dc3545;
}

.fa-book {
  color: #0052cc;
}

.fa-basketball-ball {
  color: #fa8320;
}

body {
  font-family: 'Muli', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
  padding-top: 54px;
  color: #868e96;
  background-color: #f2f2f2;
}

@media (min-width: 992px) {
  body {
    padding-top: 0;
    padding-left: 17rem;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Saira Extra Condensed', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
  font-weight: 700;
  text-transform: uppercase;
  color: #343a40;
}

h1 {
  font-size: 6rem;
  line-height: 5.5rem;
}

h2 {
  font-size: 3.5rem;
}

h3 {
  font-size: 2rem;
}

.subheading {
  text-transform: uppercase;
  font-weight: 500;
  font-family: 'Saira Extra Condensed', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
  font-size: 1.5rem;
}

.bg-primary {
  background-color: #4479a2 !important;
}

.text-primary {
  color: #4479a2 !important;
}

a {
  color: #4479a2;
}

a:hover, a:focus, a:active {
  color: #1e3648;
}

